
import React, { useEffect, useState } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal,
    InputGroup,
    FormControl,
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import axios from 'axios';
var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
});
function UpdateAgence(props) {
    const [show, setShow] = useState(false);
    const [zonesUpdate] = useState(props.zone)
    const [agences, setagences] = useState([])
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [newLivreur, setnewLivreur] = useState('')

    useEffect(() => {
        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios
            .get(urlbackend + "/agence", config)
            .then((response) => {
                console.log("this respon list", response.data);
                setagences(response.data.data)
                // this.setState({ agences: response.data.data });
            })
            .catch((e) => {
                console.log(e);
            });

    }, [])
    const handlSubmit = () => {
        console.log(newLivreur);
        console.log(props.Listcolis);

        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        if (!newLivreur) {

            alert("merci de choisir un Livreur")
        } else {
            axios.put(urlbackend + "/delivery/update/agence",
                {
                    "agence": newLivreur,
                    "colis": props.Listcolis.map(e => e.colis?.id)
                }, config)
                .then(response => {
                    alert("add Succefull")
                    console.log(response.data)
                    props.changestate()
                    handleClose()

                })
                .catch(e => {
                    console.log(e)
                })
        }

    };


    return (
        <>
            <Button variant="info" style={{ "float": "right" }}
                onClick={handleShow}> <span className="fa fa-pencil"></span>  </Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Affecture les colis pour un autre lagence   </Modal.Title>
                </Modal.Header>
                <Modal.Body>



                    <Row>                                  <Col md={12}>

                        <InputGroup className="mb-3">
                            <FormControl
                                as="select"
                                id="agence"
                                custom
                                onChange={(e) => setnewLivreur(e.target.value)}
                                onBlur={(e) => setnewLivreur(e.target.value)}
                                value={newLivreur}
                            >
                                <option>changer Livreur</option>

                                {agences.map((agence, index) => (
                                    <option key={agence.id} value={agence.id}>
                                        {agence.name}
                                    </option>
                                ))}
                            </FormControl>
                        </InputGroup>



                    </Col>

                    </Row>


                </Modal.Body>
                <Modal.Footer>
                    <Button variant="info" onClick={handlSubmit}>
                        Enregistrer
                    </Button>
                    <Button variant="secondary" onClick={handleClose}>
                        fermer
                    </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default UpdateAgence