import React from "react";

//import { PageTitle } from '../../layout-components';
import { IconButton, Box, Card, CardContent } from "@material-ui/core";
import { InputGroup, FormControl, DropdownButton, Dropdown, Table } from "react-bootstrap";
import axios from "axios";
import jsPDF from "jspdf";
import "jspdf-autotable";
import { Row, Col, Tabs, Tab, } from "react-bootstrap";
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';
import moment from 'moment'
//import TemplatePrint from '../../Template/TemplatePrint'
import config from '../../Config/config'
import Updatedriver from './Update.driver'
import UpdateAgence from "./Update.agence";
import Aux from "../../../hoc/_Aux";
var urlbackend = require("../../../env/env")();
const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const fileExtension = '.xlsx';

class ListManifesteLivraison extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            pendingPackages: [],
            ongoingPackages: [],
            enlevePackages: [],

            drivers: [],
            driver: "",
            idprovider: "",
            providers: [],
            driveriformationanifeste: {},
            typemanifest: "Tout",
            chauffeur: ""
        };
    }
    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };




        var getdrivers = axios.get(urlbackend + "/driver", config);
        var getproviders = axios.get(urlbackend + "/provider", config);

        axios
            .all([getdrivers, getproviders])
            .then(
                axios.spread((...response) => {
                    this.setState({ providers: response[0].data.data });
                    this.setState({ drivers: response[1].data.data });
                    console.log(response[0].data)
                    /*     this.setState({ pendingPackages: response[0].data.data.data });
                        this.setState({ ongoingPackages: response[1].data.content });
                        this.setState({ enlevePackages: response[2].data.content });
    
                        this.setState({ drivers: response[3].data.content }); */
                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });

    }
    getlistpickups(provider, typemanifeste) {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        var table = this.state.providers
        this.setState({ driveriformationanifeste: table.filter(e => e.id === provider)[0] })


        console.log("/admin/manifest/delivery/" + provider + "?status_delivery=" + typemanifeste)

        var getpendingPackages = axios.get(
            urlbackend + "/admin/manifest/delivery/" + provider + "?status_delivery=" + typemanifeste,
            config
        )
        axios
            .all([getpendingPackages])
            .then(
                axios.spread((...response) => {
                    response[0].data.data.length === 0 ? this.setState({ pendingPackages: [] }) : this.setState({ pendingPackages: response[0].data.data });
                    //   this.setState({ drivers: response[1].data.data });
                    console.log(response[0].data)
                  
                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });



    }


    savedriver() {
        //

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        if (!this.state.iddriver) {
            alert("choisir un driver")

        } else {
            axios.put(
                urlbackend + "/admin/delivery/driver",
                { driver: this.state.iddriver, provider: this.state.idprovider },
                config
            )
                .then((response) => {
                    alert("add Succefull");
                    console.log("response.data", response.data);
                    this.componentDidMount();
                    //this.reloaddata()
                    this.setState({ drivers: [] });
                    this.setState({ pendingPackages: [] });
                })
                .catch((e) => {
                    console.log(e);
                });


        }
    }
    reloaddata() {
        this.componentDidMount()
    }

    changecolor(valeur) {
        switch (valeur) {
            case 0:
                return "#eeeeca"
            case 1:
                return "#adcadc"
            case 2:
                return "#eeeee1"
            case 3:
                return "#dcadb1"
            default:
                break;
        }

    }
    exportPDF = (type, list) => {
        console.log(this.state.driveriformationanifeste)
        if (type === "csv") {
            var listdownload = list.map((e) => ({
                "Numéro colis": e.colis.index_colis,
                "Expéditeur": e.colis.provider.username,
                "N. de téléphone Expéditeur": e.colis.provider.phone_number,
                "client ": e.colis.name_complete,
                "N. de téléphone": e.colis.phone_number,
                "gouvernorat": e.colis.gouvernorat, "addresse": e.colis.city + ":" + e.colis.address,
                "prix": e.colis.price, "type paiment": e.colis.payment_mode,
                "type d'envoi": e.colis.type_envoi

            }))
            const ws = XLSX.utils.json_to_sheet(listdownload);
            const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
            const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
            const data = new Blob([excelBuffer], { type: fileType });
            FileSaver.saveAs(data, "fileName" + fileExtension);
        } else {

            const unit = "pt";
            const size = "A4"; // Use A1, A2, A3 or A4
            const orientation = "landscape"; // portrait or landscape

            // const marginLeft = 40;
            const doc = new jsPDF(orientation, unit, size);

            doc.setFontSize(12);

            const driverfirstname = this.state.driveriformationanifeste.firstname
            const driverlastname = this.state.driveriformationanifeste.lastname
            const usernamedriver = this.state.driveriformationanifeste.username
            const driverphone_number = this.state.driveriformationanifeste.phone_number
            //const descriminator =this.state.driveriformationanifeste.descriminator

            const headers = [["validation","Numéro colis", "Client", "N. de téléphone", "Gouvernorat addresse", "prix", "Expéditeur", "N°Expéditeur", "type"]];

            const data = list.map(elt => [ "",elt.colis.index_colis, elt.colis.name_complete, elt.colis.phone_number, elt.colis.address + " : " + elt.colis.city + " : " + elt.colis.gouvernorat, elt.colis.price + '(' + elt.colis.payment_mode + ')', elt.colis.provider.username, elt.colis.provider.phone_number, elt.colis.type_envoi]);

            let content = {
                startY: 120,
                startX: 10,
                head: headers,
                body: data,
                theme: "grid",
                styles: { "fontStyle":'bold' , 'color':"#000000"}

            };
            var image = new Image();
            image.src = config.images;
            doc.addImage(image, 'PNG', 600, 10, 160, 60);
            doc.text(config.matricule, 600, 90);
            doc.text(config.addresse1, 600, 100);
            doc.text(config.addresse2, 600, 110);
            doc.rect(280, 40, 2, 50, 'F');
            doc.rect(500, 40, 2, 52, 'F');
            doc.rect(280, 40, 220, 2, 'F');
            doc.rect(280, 90, 220, 2, 'F');
            // doc.rect(20, 40, 2, 50, 'F');
            // doc.rect(20, 40, 2, 52, 'F');
            // doc.rect(280, 40, 220, 2, 'F');
            // doc.rect(280, 90, 220, 2, 'F');
            doc.setFont(undefined, 'bold')
            doc.text(("Bon de sortie"), 300, 60);
            doc.text(("Date :" + new moment().format('DD -MM-YYYY  HH:mm')), 300, 80);
            doc.text(("Chauffeur :" + driverfirstname + " " + driverlastname), 50, 40);
            doc.text(("N. de téléphone :" + driverphone_number), 50, 60);
            doc.text(("Total:" + list.length + " colis"), 50, 80);

           
            doc.autoTable(content);
            doc.save("report.pdf")
        }

    }
    render() {
        return (
            <Aux>

                <Row>
                    <Col>


                        <hr />
                        <Tabs defaultActiveKey="home">
                            <Tab eventKey="home" title="">
                                <Card className="card-box mb-4">
                                    <div className="card-header">



                                        <div className="card-header--title">
                                            <Row>
                                                <Col md={12} xl={8}>
                                                    {(this.state.pendingPackages.length !== 0) && <DropdownButton
                                                        title={<span className="fa fa-download"> bon de livraison</span>}
                                                        variant="success"
                                                        id={`dropdown-variants-success`}
                                                        key="success"
                                                        onSelect={(e) => this.exportPDF(e, this.state.pendingPackages)}
                                                    >
                                                        <Dropdown.Item eventKey="pdf">pdf</Dropdown.Item>
                                                        <Dropdown.Item eventKey="csv">excel </Dropdown.Item>

                                                    </DropdownButton>}
                                                </Col>

                                            </Row>
                                        </div>

                                        <Box className="card-header--actions">
                                            <IconButton
                                                size="small"
                                                color="primary"
                                                className="text-primary"
                                                title="View details"
                                            ></IconButton>
                                        </Box>
                                        <Row>
                                            <Col md={12} xl={8}>    <InputGroup className="mb-3">
                                                <FormControl
                                                    as="select"
                                                    id="{this.state.driver}"
                                                    onChange={(e) => {
                                                        this.setState({ chauffeur: e.target.value });
                                                        this.getlistpickups(e.target.value, this.state.typemanifest)
                                                    }
                                                    }
                                                    custom
                                                >
                                                    <option>Choisir un chauffeur</option>

                                                    {this.state.providers.map((driver, index) => (
                                                        <option
                                                            className=" badge badge-info"
                                                            key={driver.id}
                                                            value={driver.id}
                                                        >
                                                            {driver.username}
                                                        </option>
                                                    ))}
                                                </FormControl>
                                            </InputGroup>
                                            </Col>

                                            <Col md={12} xl={2}>

                                                <InputGroup className="mb-3">
                                                    <FormControl
                                                        as="select"
                                                        id="type_envoi"
                                                        custom
                                                        onChange={(e) => {
                                                            this.setState({ typemanifest: e.target.value });
                                                            this.getlistpickups(this.state.chauffeur, e.target.value,)
                                                        }
                                                        }
                                                        onBlur={(e) => this.setState({ typemanifest: e.target.value })}
                                                        value={this.state.typemanifest}
                                                    >
                                                        <option key={2}>Tout</option>

                                                        {[{ statepay: "Au depot", valuep: 0 }, { statepay: "En cours", valuep: 1 }].map((type_envoi, index) => (
                                                            <option key={type_envoi.valuep} value={type_envoi.valuep}>
                                                                {type_envoi.statepay}
                                                            </option>
                                                        ))}
                                                    </FormControl>
                                                </InputGroup>
                                            </Col>
                                            <Col md={12} xl={2}>

                                                {(this.state.pendingPackages.length !== 0) &&
                                                    <div>
                                                        <Updatedriver Listcolis={this.state.pendingPackages} changestate={() => this.setState({ pendingPackages: [] })} />
                                                        <UpdateAgence Listcolis={this.state.pendingPackages} changestate={() => this.setState({ pendingPackages: [] })} />

                                                    </div>

                                                }
                                            </Col>
                                        </Row>



                                    </div>
                                    <CardContent className="p-0">
                                        {/* <p>Liste colis pour Pickup</p> */}
                                        <div className="table-responsive">
                                            <Table striped bordered hover responsive>
                                                <thead className="table thead-light mb-0">
                                                    <tr>
                                                        <th className="text-center">N colis</th>
                                                        <th className="text-center">Client</th>
                                                        <th className="text-center">address client</th>
                                                        <th className="text-center">Expéditeur</th>

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.pendingPackages.map((pickup, index) => (
                                                        <tr key={index} >
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.index_colis}
                                                                    <span >

                                                                    </span>
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.name_complete} : {pickup.colis.phone_number}
                                                                    <span >

                                                                    </span>
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    <h4>{pickup.colis.gouvernorat} </h4>
                                                                    {pickup.colis.city}:{" "}{pickup.colis.address}
                                                                </div>
                                                            </td>

                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.provider.username}
                                                                </div>
                                                            </td>



                                                        </tr>
                                                    ))}
                                                </tbody>
                                            </Table>
                                        </div>
                                    </CardContent>
                                </Card>
                            </Tab>
                        </Tabs>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListManifesteLivraison;
